import React, { Component } from 'react';

import './App.css';
import theme from '../src/utils/theme';

import { Grid,CardContent} from "@material-ui/core";
import Container from '@material-ui/core/Container';
import { withStyles } from '@material-ui/core/styles';


import {ROLL_NUMBER} from '../src/utils/constants';
import Header from './components/Header';
import Invoice from './components/Invoice';
import TotalAmountByCompanyCode from './components/TotalAmountByCompanyCode';
import Footer from './components/Footer';
import ComponentTwo from './components/ComponentTwo';
import Search from './components/Search';
import Chat from './components/Chat';




const styles = (theme) => ({
  '@global': {
    '*::-webkit-scrollbar': {
      width: '0.4em',
      height: '0.4em',
    },
    '*::-webkit-scrollbar-track': {
      '-webkit-box-shadow': 'inset 0 0 6px rgba(0,0,0,0.00)',
    },
    '*::-webkit-scrollbar-thumb': {
      backgroundColor: '#6D7183',
      outline: '1px solid slategrey',
    },
  },
  mainBackground: {
    background: theme.palette.primary.main,
   
    width: '100vw',
    overflow: 'hidden',
  },
  root: {
    flexGrow: 1,
  },
  paper: {
    height: 180,
    width: 100,
  },
});
class App extends Component {
  render() {
    console.log('theme', theme);
    const { classes, user } = this.props;
    return (
      // <MuiThemeProvider theme={theme}>
      <div className={classes.mainBackground}>
    
        <div className="maincntr">
        <Header/>
    
        <Container>
          <Grid spacing={1}>
            <Grid md={12}>
              <div className="hrf">
            <ComponentTwo/>
            </div>
            </Grid>
          </Grid>
    
        <div className="rowl">
        <div className="column one">
          
            <div className="sds">
          {/* <Typography variant="h6" align="center">Total Amount By company Code</Typography> */}
        
     <TotalAmountByCompanyCode/>
   
  </div>
  <div className="">
    <br/>
    <Search/>
  </div>
  </div>
          
          
   
           <div className="column two">
            
            <div className="fullTab">
             <Invoice/>
             
             </div>
   
             <Footer/>
          </div>
          </div>
  
        </Container>
      </div>
      </div>
  
    );
  }
}

export default withStyles(styles, { withTheme: true })(App);
