import React, {Component } from 'react';
import { CardContent, TableBody } from "@material-ui/core";
import Table from '@material-ui/core/Table';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import axios from 'axios';

import { BrowserRouter as Router, Route, Link } from "react-router-dom";

import "../index.css";
import SearchBox from './SearchBox';


const API_URL = 'http://localhost:8080/1704189/searchservlet';

// function searchingFor(term)
// {
//     return function (x){
//         x.customer_name.toLowerCase().includes(term.toLowerCase() );
//     }
// }

class Search extends Component {
   
    componentDidMount() {
        const url = `${API_URL}`;
        axios.get(url).then(response => response.data)
        .then((data) => {
          this.setState({ invoices: data })
          console.log(this.state.invoices)
         })
      }
    constructor(props) {
        super(props);
        this.state={
            invoices: [],
            term:''
        }
        this.searchHandler=this.searchHandler.bind(this);
    }
    /**
     * @param {{ target: { value: any; }; }} event
     */
    searchHandler =(event) =>
    {
        this.setState({term:event.target.value})
    }
    

      
    render() {
        
        const filterCustomer= this.state.invoices.filter(invoices => {
          
            return invoices.customer_name.toLowerCase().includes(this.state.term.toLowerCase());
        })
       
        return (
            <div>
                  <div className="searcharea">
         <CardContent>
         <div className="search">
         <SearchBox searchchange={this.searchHandler} />
         
                                  
            </div> 

             <div className="connT">
                      <Table stickyHeader aria-label="sticky table" >
                          <TableHead>  
            <TableRow>
                <TableCell align="left" size="small" sortDirection="asc" variant="head"  style={{fontSize:"14px", color:"#fff", fontWeight:"600"}}> Customer Name </TableCell>
                <TableCell align="left" size="small" sortDirection="asc" variant="head"  style={{fontSize:"14px", color:"#fff", fontWeight:"600"}}> Customer Number </TableCell>
                <TableCell align="left" size="small" sortDirection="asc" variant="head"  style={{fontSize:"14px", color:"#fff", fontWeight:"600"}}> Open Amount </TableCell>
            </TableRow>
            </TableHead>
            {
            filterCustomer.map(invoicer => 
                <TableBody id={invoicer.customer_number} key='invoicer.customer_number'>
                  
                    <TableCell> <Link style={{color:"#FFE4E1", textDecoration:"none", textTransform:"uppercase", fontSize:"14px"}} to={`/search/${invoicer.customer_number}`}>{invoicer.customer_name}</Link></TableCell>
                    <TableCell>   <Link style={{color:"#FFE4E1", textDecoration:"none", textTransform:"uppercase", fontSize:"14px"}} to={`/search/${invoicer.customer_number}`}> {invoicer.customer_number}  </Link></TableCell>
                    <TableCell style={{color:"#FFE4E1"}}>{invoicer.actual_open_amount}</TableCell>
                
                    {/* <Route
        path={`profile/:customer_number`}
        render={props => <ScreenTwo data={invoicer} {...props} />}
      /> */}
                
                </TableBody>
            )}
        </Table>
            </div>                  
         </CardContent>
         </div>
            </div>
        )
    }
}

export default Search;