import React, { Component } from 'react';
import AppBar from '@material-ui/core/AppBar/AppBar';
import {Toolbar, Typography, Button, Grid}  from '@material-ui/core';
import logo from '../assets/companyLogo.svg';
import voiceIcon from '../assets/voiceIcon.svg'
import "../index.css";
import Dialog from '@material-ui/core/Dialog';
import Chat from './Chat';
class Header extends Component {
  state = {
    open: false
  };

  handleClickOpen = () => {
    this.setState({ open: true });
  };

  handleClose = () => {
    this.setState({ open: false });
  };
    render() {
        return (
            <div style={{maxHeight:"100px", height:"60px"}} >
              <Toolbar>
<AppBar style={{position:"fixed", backgroundColor:"#000033"}} position="fixed" >
    <Grid spacing={4}>    
    <Grid md={6} xs={6}>
      <img
        alt=""
        src={logo}
        style={{alignItems:"center", zIndex:"inherit", marginLeft:"40px", display:"flex", height:"30px", width:"50px"}}
      />

</Grid>
<Grid md={6} xs={6}>
<Typography variant="h5" style={{marginLeft:"120px", marginTop:"-20px"}} color="inherit" > ABC Company</Typography> 
</Grid>
</Grid>

     
    
    <div className="hrbtn">
    <Button autoid="professor-button" onClick={this.handleClickOpen} variant="contained" size="large" style={{padding:"2px", borderRadius:"25px", color:"#fff", 
    backgroundColor: "#F53803",
backgroundImage: "linear-gradient(147deg, #F53803 0%, #F5D020 74%)", marginTop:"-8px"}}>
  
      Proffesor
      <img src={voiceIcon} alt="voiceIcon"/>
      </Button>
      <Dialog
      className="chatbot"
          open={this.state.open}
         
          keepMounted
          onClose={this.handleClose}
          aria-labelledby="alert-dialog-slide-title"
          aria-describedby="alert-dialog-slide-description"
        >
          {/* <Chat/> */}
     
          <iframe title="professorBot" width="350" height="430" allow="microphone;" src="https://console.dialogflow.com/api-client/demo/embedded/e436aab8-696d-40e2-90c5-ef67d838d958"></iframe>
      
          <Button autoid="professor-close-button" onClick={this.handleClose} color="primary">
              Close
            </Button>
        </Dialog>
    </div>
   
  </AppBar>
  </Toolbar>
            </div>
        );
    }
}

export default Header;