// @ts-nocheck
import React, { Component } from 'react';
import PropTypes from "prop-types";
import IconButton from '@material-ui/core/IconButton';
import { withStyles } from "@material-ui/core/styles";
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import {Link} from "react-router-dom";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import TableSortLabel from "@material-ui/core/TableSortLabel";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import Checkbox from "@material-ui/core/Checkbox";
import { Button, Grid } from "@material-ui/core";
import { lighten } from "@material-ui/core/styles/colorManipulator";
import axios from "axios";
import {predictInvoice} from "./service";


const API_URL = 'http://localhost:8080/1704189/ShowServlet';


const rows = [
  {
    id: "0",
    numeric: false,
    disablePadding: true,
    label: "Company ID",
  },
  { id: "1", numeric: true, disablePadding: false, label: "Account Header ID" },
  { id: "2", numeric: true, disablePadding: false, label: "Document Number" },
  { id: "3", numeric: true, disablePadding: false, label: "Business Code" },
  { id: "4", numeric: true, disablePadding: false, label: "Document Type" },
  { id: "5", numeric: true, disablePadding: false, label: "Customer Number" },
  { id: "6", numeric: true, disablePadding: false, label: "Customer Map ID" },
  { id: "7", numeric: true, disablePadding: false, label: "Name Of Customer" },
  { id: "8", numeric: true, disablePadding: false, label: "Document Create Date"},
  { id: "9", numeric: true, disablePadding: false, label: "Baseline Date" },
  { id: "10", numeric: true, disablePadding: false, label: "Invoice Date" },
  { id: "11", numeric: true, disablePadding: false, label: "Invoice ID" },
  { id: "12", numeric: true, disablePadding: false, label: "Total Open Amount"},
  { id: "13", numeric: true, disablePadding: false, label: "Customer Payment Terms"},
  { id: "14", numeric: true, disablePadding: false, label: "Clear Date" },
  { id: "15", numeric: true, disablePadding: false, label: "Is Open Invoice" },
  { id: "16", numeric: true, disablePadding: false, label: "Shipping Date" },
  { id: "17", numeric: true, disablePadding: false, label: "Payment Amount" },
  { id: "18", numeric: true, disablePadding: false, label: "Days past Due date"},
  { id: "19", numeric: true, disablePadding: false, label: "Doc Id" },
  { id: "20", numeric: true, disablePadding: false, label: "Document Create Date"},
  { id: "21", numeric: true, disablePadding: false, label: "Actual Amount Outstanding"},
  { id: "22", numeric: true, disablePadding: false, label: "Age of Invoice" },
  { id: "23", numeric: true, disablePadding: false, label: "Invoice Currency" },
  { id: "24", numeric: true, disablePadding: false, label: "Predicted Payment Type"},
  { id: "25", numeric: true, disablePadding: false, label: "Predicted Amount" },
];

class EnhancedTableHead extends React.Component {
  render() {
    const {
      onSelectAllClick,
      order,
      orderBy,
      numSelected,
      rowCount,
    } = this.props;

    return (
      <TableHead>
        <TableRow>
          <TableCell padding="checkbox">
            <Checkbox
              indeterminate={numSelected > 0 && numSelected < rowCount}
              checked={numSelected === rowCount}
              onChange={onSelectAllClick}
              style={{ color: "white" }}
            />
          </TableCell>
          {rows.map(
            (row) => (
              <TableCell
                key={row.id}
                align={row.numeric ? "right" : "left"}
                padding={row.disablePadding ? "none" : "default"}
                sortDirection={orderBy === row.id ? order : false}
                style={{ color: "white" }}>
                <TableSortLabel direction={order} style={{ color: "white" }}>
                  {row.label}
                </TableSortLabel>
              </TableCell>
            ),
            this
          )}
        </TableRow>
      </TableHead>
    );
  }
}

EnhancedTableHead.propTypes = {
  numSelected: PropTypes.number.isRequired,
  onRequestSort: PropTypes.func.isRequired,
  onSelectAllClick: PropTypes.func.isRequired,
  order: PropTypes.string.isRequired,
  orderBy: PropTypes.string.isRequired,
  rowCount: PropTypes.number.isRequired,
};

const styles = (theme) => ({
  root: {
    marginRight: theme.spacing.unit * 2,
    marginLeft: theme.spacing.unit * 2,
    marginTop: theme.spacing.unit * 2,

  },
  table: {
    minWidth: 1020,
    padding: '15px',
    marginLeft:"10px",
  
  },
  tableWrapper: {
    overflowX: "auto",
    marginLeft:"-5%",
    marginRight:"-3%",
    height: 450,
  },

});

class Invoice extends React.Component {

 

  state = {
  
    selected: [],
    data: [],
    pred:[],
    page: 0,
    rowsPerPage: 10,
    openDialog: false,
    selecteddata: null,
  };
  
  componentDidMount() {
    const url = `${API_URL}`;
    axios.get(url).then(response => response.data)
    .then((data) => {
      this.setState({ data: data })
      console.log(this.state.data)
     })
  }


  getAll() {
    console.log(this.state.data.length);
    var data = [];
    for (var i = 1; i <= this.state.data.length; i++) data.push(i);
    return data;
  }

  handleSelectAllClick = (event) => {
    if (event.target.checked) {
      this.setState({
        selected: this.getAll(),
      });
      return;
    }
    this.setState({ selected: [] });
  };

  handleClick = (event, id) => {
    const { selected } = this.state;
    const selectedIndex = selected.indexOf(id);
    console.log(selectedIndex);
    let newSelected = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, id);
      console.log(newSelected);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1)
      );
    }

    this.setState({ selected: newSelected });
  };

  handleChangePage = (event, page) => {
    this.setState({ page });
  };

  handleChangeRowsPerPage = (event) => {
    this.setState({ rowsPerPage: event.target.value });
  };

  isSelected = (id) => this.state.selected.indexOf(id) !== -1;

  parse(data) {
    var dt = [];
    for (var i = 0; i < data.length; i++) dt.push({ id: i + 1, ...data[i] });
    return dt;
  }

  handleDialog(data) {
    console.log(this.state)
        for(var i=0; i<data.length; i++)
        {
            if(data[i].id === this.state.selected[0])
            {
                this.setState({
                  selecteddata: data[i],
                  openDialog: true,
                })
                break;
            }
        }
  }


  handlemodifyClose() {
    this.setState({
      openDialog: false,
      selecteddata: null,
    })
    window.location.reload(true);
  }
  

  predictButon =() =>{
    console.log("selected", this.state.selected)
    const invPred = this.state.data.filter(item => this.state.selected.indexOf(item.pk_id) !== -1)
    console.log(invPred);
    predictInvoice(invPred). then(data =>{
      if(data)
        this.setState({
          pred: this.state.pred.concat(data)
        }, () => {console.log(this.state.pred[0]) })
    })
  }
  render() {

    const { classes } = this.props;
    const { order, orderBy, selected, rowsPerPage, page } = this.state;
    const data = this.parse(this.state.data);
    const emptyRows = rowsPerPage - Math.min(rowsPerPage, data.length - page * rowsPerPage);
    return (
      <div>
     
      
          <div className={classes.tableWrapper}>
            <div className="predHead">
              <Grid spacing={12}>
                <Grid md={1} xs={1}>
          <Typography variant="h4" style={{fontSize:"24px", color:"#fff",  marginLeft:"40px", padding:"3px"}}>Invoices</Typography>
          </Grid>

          <Grid md={11} xs={11}>
          {selected.length >= 1 ? (
              <Button
              style={{float:"right",  marginTop:"-30px",backgroundColor: "#09203F",
              backgroundImage:" linear-gradient(315deg, #09203F 0%, #537895  74%)",  color:"#fff"}} variant="contained" autoid="predict-button"
                onClick={this.predictButon}>
                Predict
              </Button>
            ) : (
              <Button
              autoid="predict-button"
                variant="outlined"
                className={classes.buttonDisabled}
                style={{float:"right",  marginTop:"-30px", color: "#90a4ae", borderColor: "#90a4ae" }}
                disabled>
                Predict
              </Button>
            )}
         
            </Grid>
            </Grid>
            </div>
        
{/*                     
                     <Button style={{float:"right", marginRight:"20px", marginTop:"-30px",backgroundColor: "#fff000",
backgroundImage:" linear-gradient(315deg, #fff000 0%, #ed008c  74%)", padding:"10px", color:"#fff"}} variant="contained" autoid="predict-button">Predict</Button> */}
            <Table autoid="invoice-table-collector" className={classes.table} aria-labelledby="tableTitle">
              <EnhancedTableHead
                numSelected={selected.length}
                order={order}
                orderBy={orderBy}
                onSelectAllClick={this.handleSelectAllClick}
                rowCount={data.length}
                value={data}
              
               />

              <TableBody>
                {data.length !== 0 &&
                  data
                    .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                    .map((n) => {
                      const isSelected = this.isSelected(n.id);
                      return (
                        <TableRow
                          hover
                          onClick={(event) => this.handleClick(event, n.id)}
                          role="checkbox"
                          aria-checked={isSelected}
                          tabIndex={-1}
                          key={n.id}
                          selected={isSelected}
                        >
                          <TableCell padding="checkbox">
                            <Checkbox
                              checked={isSelected}
                              style={{ color: "white" }}
                            />
                          </TableCell>
                          <TableCell
                            component="th"
                            scope="row"
                            padding="none"
                            style={{ color: "white" }}
                          >
                            {n.company_id}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.acct_doc_header_id}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.document_number}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.business_code}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.doctype}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.customer_number}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.fk_customer_map_id}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.customer_name}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.document_create_date}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.baseline_create_date}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.invoice_date_norm}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.invoice_id}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.total_open_amount}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.cust_payment_terms}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.clearing_date}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.isOpen}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.ship_date}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.paid_amount}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.dayspast_due}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.document_id}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.document_creation_date}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.actual_open_amount}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.invoice_age}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                            {n.invoice_amount_doc_currency}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                          {this.state.pred.filter((item) => item.id == n.id).length > 0 ? (this.state.pred.filter(item => item.id == n.pk_id)[0].predictions <n.actual_open_amount ? "PARTIAL": "FULL"):null}
                          </TableCell>
                          <TableCell align="right" style={{ color: "white" }}>
                          {this.state.pred.filter(item => item.id == n.pk_id).length > 0 ? Number(this.state.pred.filter(item => item.id == n.pk_id)[0].predictions).toFixed(2):null}
                      
                       
                       {/* {this.state.pred.map(prd => (prd.predictions))} */}
                          </TableCell>
                        </TableRow>
                      );
                    })}
                {emptyRows > 0 && (
                  <TableRow style={{ height: 49 * emptyRows }}>
                    <TableCell colSpan={6} />
                  </TableRow>
                )}
              </TableBody>
            </Table>
            
          </div>
      
        <TablePagination
        autoid="invoice-table-pagination-collector"
          rowsPerPageOptions={[10, 15 , 20]}
          component="div"
          count={data.length}
          rowsPerPage={rowsPerPage}
          page={page}
          backIconButtonProps={{
            autoid:"pagination-button-previous-collector",
            "aria-label": "Previous Page",
          }}
          nextIconButtonProps={{
            autoid:"pagination-button-next-collector",
            "aria-label": "Next Page",
          }}
          onChangePage={this.handleChangePage}
          onChangeRowsPerPage={this.handleChangeRowsPerPage}
          style={{ color: "white" }}
        />
  
      </div>
    );
  }
}

Invoice.propTypes = {
  classes: PropTypes.object.isRequired,
};


export default withStyles(styles, { withTheme: true })(Invoice);
