// const mystore= createStore(reducer);


var dataset= crossfilter(data);
console.log(dataset);
var companyCodeDim = dataset.dimension((d) => d.bussiness_code);
var amountGroup = companyCodeDim.group().reduceSum((d) => d.actual_open_amount);

var companyCodeDimm = dataset.dimension((d) => d.bussiness_code);
var amountGroup1 = companyCodeDimm.group().reduceSum((d) => d.actual_open_amount);

var companyCodeDim2 = dataset.dimension((d) => d.bussiness_code);
var totalDelay = companyCodeDim2.group().reduceSum((d=>d.dayspast_due));
var countDelay = companyCodeDim2.group().reduceSum((d=>d.dayspast_due));

var companyCodeDim3 = dataset.dimension((d) => d.bussiness_code);
var openiv = companyCodeDim3.group().reduceSum((d=>d.isOpen));


function prepareData(groups) {
  var categories = [];
  var data = [];

  var gdata = groups.top(4);
  gdata.forEach((d) => {
    categories.push(d.key);
    data.push(d.value);
  });
  return {
    categories: categories,
    data: data,
  };
}

function openAr(groups)
{
  var tar;
  var gdata= groups.all();
  gdata.forEach((d) =>{
   tar = d.value;
  })
  return{
        tar:tar
  };
}

function openInvoice(groups)
{
  var invoice = 0;
  var gdata= groups.all();
  gdata.forEach((d) =>{
    if(d.value == 1)
    {
      invoice += d.value;
    }
  })
  return{
      invoice:invoice
  };
}


var tempObject = prepareData(amountGroup);
var tempObject1= openAr(amountGroup1);
console.log(tempObject1);


var tempObject2= openInvoice(openiv);
console.log(tempObject2);

const options = {
  chart: {
    className: "analyticsg",
    type: "bar",
    borderColor: "#fff",
    backgroundColor:"#000033",
    height:180,
    borderRadius: 5,
    borderWidth: 2,
    renderTo: "analytics",
  },
  title: {
    text: "Total Open Amount By company code",
    style:{
      color:"#fff",
      fontSize:"14px",

    }
  },
  exporting: {
    buttons: {
      contextButton: {
        enabled: false,
      },
    },
  },
  credits: {
    enabled: false,
  },
 
  xAxis: {
    categories: tempObject.categories,
    title: {
      text: "Company Code",
      style: {
        color: "#fff",
        fontWeight: "bold",
        fontSize: "12px",
      },
    },
    labels: {
      rotation: -45,
      style: {
        fontSize: "13px",
        fontFamily: "Verdana, sans-serif",
      },
    },
  },
  yAxis: {
  
    min: 0,
    title: {
      text: "Total Amount",
      style: {
        color: "#fff",
        fontWeight: "bold",
        fontSize: "12px",
      },
    },
    tickInterval: "25",
    color:"#fff",
  },
  plotOptions: {
    series: {
      colorByPoint: true,
      point: {
        events: {
          click: function () {
            this.select(null, true);
            var selectedPoint = this.series.chart.getSelectedPoints();
            console.log(selectedPoint);
            var filteredPoints = [];

            for (let index = 0; index < selectedPoint.length; index++) {
              filteredPoints.push(selectedPoint[index].category);
            }

            //console.log(filteredPoints)

            function multiVariateFilter(values) {
              return function (v) {
                return values.indexOf(v) !== -1;
              };
            }

            if (filteredPoints.length > 0) {
              companyCodeDim.filterFunction(multiVariateFilter(filteredPoints));
            } else companyCodeDim.filterAll();

         

      
          },
        },
      },
    },
  },
  legend: {
    enabled: false,
  },
  tooltip: {
    pointFormat: "<b>{point.y:.1f}</b>",
  },
  series: [
    {
      name: "Amount",
      data: tempObject.data,
      dataLabels: {
        enabled: false,
        rotation: -90,
        color: "#fff",
        align: "right",
        format: "{point.y:.1f}", // one decimal
        y: 10, // 10 pixels down from the top
        style: {
          fontSize: "13px",
          color:"#fff",
          fontFamily: "Verdana, sans-serif",
        },
      },
    },
  ],
};
// var chart2 =  Highcharts.chart(options);
class TotalAmountByCompanyCode extends Component {

  
  
  render() {
    return (
      <div>
        
        <HighchartsReact autoid="companycode-chart" highcharts={Highcharts} options={options}/>
      </div>
    );
  }
}

export default TotalAmountByCompanyCode;