import React, { Component } from 'react';
    import Pusher from 'pusher-js';
    import '../App.css';
    import Input from '@material-ui/core/Input';
import { Card, Typography } from '@material-ui/core';
import CardContent from '@material-ui/core/CardContent';
import InputAdornment from '@material-ui/core/InputAdornment';
import dglogo from './dglogo.png';
import SendIcon from '@material-ui/icons/Send';

    class Chat extends Component {
      constructor(props) {
        super(props);
        this.state = {
          userMessage: '',
          conversation: [],
        };
      }

      componentDidMount() {
        const pusher = new Pusher('a0cdec9521af924e1c86', {
          cluster: 'ap2',
          encrypted: true,
        });

        const channel = pusher.subscribe('bot');
        channel.bind('bot-response', data => {
          const msg = {
            autoid:"ai",
            text: data.message,
            user: 'ai',
          };
          this.setState({
            conversation: [...this.state.conversation, msg],
          });
        });
      }

      handleChange = event => {
        this.setState({ userMessage: event.target.value });
      };

      handleSubmit = event => {
        event.preventDefault();
        if (!this.state.userMessage.trim()) return;

        const msg = {
          autoid:"human",
          text: this.state.userMessage,
          user: 'human',
        };

        this.setState({
          conversation: [...this.state.conversation, msg],
        });

        fetch('http://localhost:4000/chat', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            message: this.state.userMessage,
          }),
        });

        this.setState({ userMessage: '' });
      };

      render() {
        const ChatBubble = (text, i, className) => {
          return (
            <div key={`${className}-${i}`} className={`${className} chat-bubble`}>
              <span className="chat-content">{text}</span>
            </div>
          );
        };

        const chat = this.state.conversation.map((e, index) =>
          ChatBubble(e.text, index, e.user)
        );

        return (
          <div>


<Card className="chatbox" variant="contained">
<div className="bothead">
<img  style={{height:"60px", width:"60px", padding:"5px"}} src={dglogo} alt="professor"/>
<Typography variant="h4" style={{fontSize:"24px", color:"#fff", textAlign:"center"}}>Professor</Typography>
</div>
<CardContent>

<div className="conversationl">{chat}</div>
<div className="message-box">
 <form onSubmit={this.handleSubmit}>
<Input autoid="professor-input-box" className="chater" autoFocus onChange={this.handleChange}  endAdornment={
              <InputAdornment position="end"> <SendIcon autoid="professor-send-button" color="inherit" onChange={this.handleChange}/> </InputAdornment> }value={this.state.userMessage}  placeholder="Type your message"/>
 </form>
 </div>
</CardContent>

 </Card>

          
          </div>
        );
      }
    }

    export default Chat;