import Axios from "axios"

export const predictInvoice = (invoices)=>{
    return Axios.post('http://localhost:5000/predict',
    {},
    {
        params:{
            data:{
                id:"1704189",
                data:invoices
            }
        }
    }
    )
    .then (response =>{
        return response.data;
    }).catch(err =>{
        console.log(err);
    })
}