import React from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import axios from "axios";

class ModifyDialog extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      open_amount: this.props.data.actual_open_amount,
      doctype: this.props.data.doctype,
      open_amount_old: this.props.data.actual_open_amount,
      doctype_old: this.props.data.doctype,
      id: this.props.data.pk_id,
      open: true,
    };
    this.handleChangeOpenAmount = this.handleChangeOpenAmount.bind(this);
    this.handleClickOpen = this.handleClickOpen.bind(this);
    this.handleChangeDoctype = this. handleChangeDoctype.bind(this);
  }

  handleClickOpen = () => {
    this.setState({ open: true });
  };

  handleChangeOpenAmount = (e) => {
    this.setState({
      open_amount: e.target.value,
    });
  };

  handleChangeDoctype = (e) => {
    this.setState({
      doctype: e.target.value,
    });
  };

  handleClose = () => {
    this.setState({ open: false });
    this.props.onClose();
  };
  handleCloseSubmit = () => {
    this.updateTable(
      this.state.open_amount,
      this.state.doctype,
      this.state.id
    );
    this.setState({ open: false });
    this.props.onClose();
  };

  updateTable(open_amount, doctype, id) {
    axios.get(`http://localhost:8080/1704189/modifytable?pk_id=${id}&actual_open_amount=${open_amount}&doctype=${doctype}`)
    .catch(e => console.log(e))
  }

  render() {
    return (
      <Dialog
        open={this.state.open}
        onClose={this.handleClose}
        aria-labelledby="form-dialog-title"
        PaperProps={{ style: { backgroundColor: "#252C48" } }}
      >
        <DialogTitle id="form-dialog-title">
          <div style={{ color: "white" }}>Modify</div>
        </DialogTitle>
        <DialogContent>
          <TextField
            id="filled-full-width"
            autoid="open-amount-input"
            label="Open Amount"
            placeholder="Don't leave this empty"
            fullWidth
            margin="normal"
            InputLabelProps={{
              shrink: true,
              style: { color: "white" },
            }}
            inputProps={{
              style: { color: "white" },
            }}
            value={this.state.open_amount}
            onChange={this.handleChangeOpenAmount}
            variant="filled"
          />
          <TextField
            id="filled-full-width"
            autoid="doctype-input"
            label="Doctype"
            placeholder="Don't leave this empty"
            fullWidth
            margin="normal"
            InputLabelProps={{
              shrink: true,
              style: { color: "white" },
            }}
            inputProps={{
              style: { color: "white" },
            }}
            value={this.state.doctype}
            onChange={this.handleChangeDoctype}
            variant="filled"
          />
        </DialogContent>
        <DialogActions>
          <Button
          autoid="modify-cancel-button"
            onClick={this.handleClose}
            style={{ color: "#90a4ae", borderColor: "#90a4ae" }}
            variant="outlined"
          >
            Cancel
          </Button>

          {(this.state.open_amount_old === this.state.open_amount ||
            this.state.open_amount === "") &&
          (this.state.doctype === this.state.doctype_old ||
            this.state.doctype === "") ? (
            <Button
      
            autoid="modify-save-button"
              onClick={this.handleCloseSubmit}
              style={{ color: "#9e9e9e" }}
              variant="contained"
              disabled
            >
              Save
            </Button>
          ) : (
            <Button
              onClick={this.handleCloseSubmit}
              style={{ color: "white", backgroundColor: "#0091ea" }}
              variant="contained"
            >
              Save
            </Button>
          )}
        </DialogActions>
      </Dialog>
    );
  }
}

export default (ModifyDialog);
