import React, { Component } from 'react';
import { Grid, CardContent, Typography } from "@material-ui/core";
import Container from '@material-ui/core/Container';
import "../index.css";
import axios from 'axios';
import { formatter } from '../utils/formatter';
const API_URL = 'http://localhost:8080/1704189/countservlet';
const findOpenAR = (data) => {
    const openARarray = data.map((d) => d.actual_open_amount);
    const totalOpenAR = openARarray.reduce((tot, item) => (tot += item), 0);
    return totalOpenAR;
  };
const findOpenInvoices = (data) => {
    const isOpenArr = data.map((d) => d.isOpen);
    const totalOpenInvoices = isOpenArr.filter((i) => i === 1).length;
    return totalOpenInvoices;
  };  
function onlyUnique(value, index, self)
{
    return self. indexOf(value) ===index;
}
  const findCustomer = (data) =>
  {
    const customer = data.map((d) => d.customer_name);
    var totalcustomer = customer.filter(onlyUnique);
    var cstLen= totalcustomer.length;
    return cstLen;
  };

  const findAvg = (data) => {
    const dueAvg = data.map((d) => d.dayspast_due);
    const totalavg = dueAvg.reduce((tot, days) => (tot += days), 0);
    const finalAvg = totalavg/dueAvg.length;
    return Math.round(finalAvg);
  }; 
 

class ComponentTwo extends Component {
    state={
        result:[]
    }

    componentDidMount() {
        const url = `${API_URL}`;
        axios.get(url).then(response => response.data)
        .then((data) => {
          this.setState({ result: data })
          console.log(this.state.result)
         })
      }
    render() {
        return (
            <div>
                <Container>
                    <Grid container spacing={8}>
                    <Grid item xs={12} md={3}>
                        <div className="glass">
         <CardContent>
         <h2 style={{fontSize:'24px', fontWeight: 600, textAlign:"center" , marginTop:"-20px"}} className='CardHead'>Total Customer</h2>
            <div className="row d-flex align-items-center">
                <div className="col-9">
                    <Typography style={{color:"#fff"}} variant="h4" align="center" autoid="total-customers-text-collector">{findCustomer(this.state.result)}</Typography>
                </div>
            </div>
         </CardContent>
         </div>
        </Grid>

        <Grid item xs={12} md={3}>
                        <div className="glass">
         <CardContent>
         <h2 style={{fontSize:'24px', fontWeight: 600, textAlign:"center", marginTop:"-20px"}} className='CardHead'>Total Open Ar</h2>
                           
        <Typography variant="h4" style={{color:"#fff"}} align="center" id="OpenArq" autoid="total-open-ar-text-collector"> {"$ " + formatter(findOpenAR(this.state.result))} </Typography>
                                 
         </CardContent>
         </div>
        </Grid>

        <Grid  item xs={12} md={3}>
                        <div className="glass">
         <CardContent>
         <h2 style={{fontSize:'24px', fontWeight: 600, textAlign:"center" , marginTop:"-20px"}} className='CardHead'>Total Open Invoice</h2>
                                <div className="row d-flex align-items-center">
                                    <div className="col-9">
        <Typography style={{color:"#fff"}}  variant="h4" align="center" id="openInvoiced" autoid="total-open-invoice-text-collector">{findOpenInvoices(this.state.result)}</Typography>
                                    </div>

                                  
                                </div>
         </CardContent>
         </div>
        </Grid>

        <Grid item xs={12} md={3}>
                        <div className="glass">
         <CardContent>
         <h2 style={{fontSize:'22px', fontWeight: 600, textAlign:"center" , marginTop:"-20px"}} className='CardHead'>Average Days Delay</h2>
                                <div className="row d-flex align-items-center">
                                    <div className="col-9">
        <Typography style={{color:"#fff"}} variant="h4" align="center" id="avg" autoid="average-days-delay-text-collector">{findAvg(this.state.result)}</Typography>
                                    </div>

                                  
                                </div>
         </CardContent>
         </div>
        </Grid>

       

                    </Grid>
                    
                </Container>
            </div>
        );
    }
}
// const mapStateToProps = state =>({
//     OpenArq: state.OpenArq,
//     openInvoiced: state.openInvoiced,
//     avg: state.avg

// })
export default ComponentTwo;